@font-face {
	font-family: 'Molot';
	src: url('../fonts/hinted-subset-Molot.eot');
	src: local('Molot Regular'), local('Molot'),
		url('../fonts/hinted-subset-Molot.eot?#iefix') format('embedded-opentype'),
		url('../fonts/hinted-subset-Molot.woff2') format('woff2'),
		url('../fonts/hinted-subset-Molot.woff') format('woff'),
		url('../fonts/hinted-subset-Molot.ttf') format('truetype'),
		url('../fonts/hinted-subset-Molot.svg#Molot') format('svg');
	font-weight: normal;
	font-style: normal;
}

