.scroll2top,.scroll2top-mini {
  font: 1.6rem 'robotoregular';
  text-transform: uppercase;
  text-align: center;
  padding: 3rem 1rem 1rem;
  margin: 0 1rem 1rem;
  color: #878787;
  text-decoration: none !important;
  border: 1px solid #878787;
  position: relative;
}

.scroll2top:after,.scroll2top-mini:after {
  content: '';
  width: 28px;
  height: 15px;
  background: 0 0 no-repeat url(data:image/svg+xml;base64,);
  background-size: cover;
  position: absolute;
  top: 10px;
  left: 50%;
  margin-left: -14px;
}

.scroll2top-mini {
  font-size: 1.2rem;
  font-weight: 300;
  position: absolute;
