  width: 9px;
  height: 9px;
  background-image: url(data:image/svg+xml;base64,);
  background-position: center;
  background-repeat: no-repeat;
  margin-left: .5rem;
}

.arr:hover {
  color: #000 !important;
  border-bottom-color: #000 !important;
}

.hasdropdown {
