}

.hasdropdown .dropdown {
  display: none;
  text-transform: none;
  text-align: left;
  position: absolute;
  top: 25px;
  right: 0;
  z-index: 1;
  border: 1px solid #878787;
  padding: .7rem 1rem;
  background-color: #fff;
}

.hasdropdown .dropdown ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.hasdropdown .dropdown ul li {
  margin-top: 3px;
  white-space: nowrap;
}

.hasdropdown .dropdown ul li:first-child {
  margin-top: 0;
}

.hasdropdown.open .dropdown {
