  .scroll2top-mini {
    width: 4rem;
    height: 4rem;
    text-indent: -10000px;
    padding: 0;
  }

  .scroll2top-mini:after {
    top: 50%;
    margin-top: -8px;
  }

  .site-header-top > .container {
    padding-left: 337px;
  }

  .site-header .searchline form input {
    width: 60px;
  }

  .nav .divider.super-divider {
    margin-right: 18px;
  }

  .nav-root > li {
    font-size: 1.4rem;
  }
}

@media only screen and (max-width: 992px) {
  .scroll2top-mini {
    right: auto;
    left: -5rem;
    bottom: -5px;
  }

  .pagination {
    padding-bottom: 30px;
  }

  .pagination .item.previous,.pagination .item.next {
    position: absolute;
    top: 35px;
  }

  .pagination .item.previous {
    left: 10px;
  }

  .pagination .item.next {
    right: 10px;
  }

  .site-header-top .phone {
    text-align: right;
  }

  .nav > .container {
    padding-left: 200px;
  }

  .nav.mobile-menu .menu-link a:hover {
    background: none;
  }

  .nav .navs,.nav .nav-special {
    float: none;
  }

  .nav .navs {
    display: block;
    width: 100%;
    max-height: 0;
    position: absolute;
    left: 0;
    background-color: #000;
    text-align: center;
  }

  .nav .navs .nav-root,.nav .navs .divider {
    max-height: 0;
    overflow: hidden;
    -webkit-transition: all .3s ease-out;
    -moz-transition: all .3s ease-out;
    -ms-transition: all .3s ease-out;
    -o-transition: all .3s ease-out;
    transition: all .3s ease-out;
  }

  .site-footer .menu .socials li a {
    width: 32px;
    height: 32px;
    line-height: 32px;
  }

  .product .add-to-cart a,.qv .add-to-cart a {
    display: block;
    width: auto;
    font-size: 1.8rem;
  }

  .product-additional .item,.qv-additional .item {
    display: inline-block;
    vertical-align: top;
    margin: 0 1rem 2rem;
  }

  .product-additional,.qv-additional {
    width: auto;
    text-align: center;
    margin-bottom: 3rem;
  }

  .loop .item {
    display: inline-block;
    vertical-align: top;
    margin: 0 0 2rem;
  }
}

@media only screen and (max-width: 767px) {
  .breadcrumbs {
    text-align: center;
  }

  .scroll2top-mini {
    display: none;
  }

  .quickorder .product-title {
    text-align: left;
  }

  .quickorder .product-price {
    text-align: left;
  }

  .categorynav-mobile {
    padding-left: 200px;
  }

  .categorynav-mobile ul li.categorynav-mobile-view {
    display: none;
  }

  .filters {
    display: block;
    position: absolute;
    width: 320px;
    transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
    background: #000000;
    top: 128px;
