    display: none;
  }

  .quickorder-wrapper {
    min-height: 390px;
  }

  .quickorder-content {
    height: 100%;
    border: none;
    margin-left: 0;
    position: absolute;
    top: 0;
    left: 0;
  }

  .quickorder-image {
    width: 125px;
  }

  .quickorder-image img {
    width: 125px;
    height: auto;
    border-right: 2px solid #000;
    border-bottom: 2px solid #000;
  }

  .quickorder .product-title {
    padding-left: 125px;
    font-size: 1.8rem;
  }

  .quickorder .product-price {
    text-align: left;
    padding-left: 125px;
    font-size: 1.8rem;
  }

  .quickorder .product-price .price-old {
    font-size: 1.4rem;
  }

  .quickorder .product-price .price-old-wrapper {
    margin-top: 0;
  }

  .quickorder .product-price .price-old .discount {
    width: 43px;
    height: 20px;
    line-height: 20px;
    background-size: cover;
    right: -50px;
    margin-top: -10px;
    font-size: 1.4rem;
    padding-left: 8px;
  }

  .quickorder form {
    width: 100%;
    margin-top: 0;
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 0 15px 60px;
  }

  .quickorder form input {
    font-size: 1.4rem;
    text-align: center;
  }

  .quickorder form button {
    display: block;
    width: 100%;
  }

  .site-header-top .phone {
    display: none !important;
  }

  .nav > .container {
    padding-left: 150px;
  }

  .nav-wrapper .logo {
    left: 10px;
  }

  .nav-root > li {
    padding: 0 .9rem;
    line-height: normal;
  }

  .categorynav-mobile {
    padding-left: 150px;
  }

  .categorynav-mobile ul li.categorynav-mobile-view {
    display: inline-block;
  }

  .site-footer .bottom .col-xs-12 {
    width: 100%;
    margin-left: 0;
  }

  .site-footer .subscribe {
    width: 100%;
    margin: 2rem 0 0 0;
  }

  .category-tools .mobile-pager {
    display: block;
  }

  .loop .item {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}
