/* =====================================================================================================================
// Skin details */

.irs-line-mid, .irs-line-left, .irs-line-right, .irs-bar, .irs-bar-edge, .irs-slider {
    background-color: #fff;
    border: 1px solid $text;
}

.irs {
    height: 50px;
}

.irs-with-grid {
    height: 60px;
}

.irs-line {
    height: 15px;
    top: 25px;
    background-color: #fff;
}

.irs-line-left {
    height: 20px;
    border: none;
}

.irs-line-mid {
    height: 15px;
}

.irs-line-right {
    height: 20px;
}

.irs-bar {
    height: 15px;
    top: 25px;
    background-color: $text;
}

.irs-bar-edge {
    top: 25px;
    height: 12px;
    width: 9px;
}

.irs-shadow {
    height: 3px;
    top: 34px;
    background: #000;
    opacity: 0.25;
}

.lt-ie9 .irs-shadow {
    filter: alpha(opacity=25);
}

.irs-slider {
    width: 2px;
    height: 28px;
    top: 18px;

    &.state_hover, &:hover {
    }
}

.irs-min, .irs-max {
    color: #999;
    font-size: 10px;
    line-height: 1.333;
    text-shadow: none;
    top: 0;
    padding: 1px 3px;
    background: #e1e4e9;
    -moz-border-radius: 4px;
    border-radius: 4px;
}

.irs-from, .irs-to, .irs-single {
    color: #fff;
    font-size: 10px;
    line-height: 1.333;
    text-shadow: none;
    padding: 1px 5px;
    background: #ed5565;
    -moz-border-radius: 4px;
    border-radius: 4px;
}

.irs-from:after, .irs-to:after, .irs-single:after {
    position: absolute;
    display: block;
    content: "";
    bottom: -6px;
    left: 50%;
    width: 0;
    height: 0;
    margin-left: -3px;
    overflow: hidden;
    border: 3px solid transparent;
    border-top-color: #ed5565;
}

.irs-grid-pol {
    background: #e1e4e9;
}

.irs-grid-text {
    color: #999;
}

.irs-disabled {
}
